import sparkmd5 from "./sparkmd5";

/**
 * 
 */
const CHUNK_SIZE = 1024 * 1024 * 5; // 5MB
const THREAD_COUNT = 4;


/**
 * 
 * 
 * {
 *    start: 
 *    end: 
 *    index: 
 *    hash: MD5
 *    content: 
 * }
 * @param {*} file 
 * @param {*} index 
 * @param {*} chunkSize 
 * @returns 
 */
export function createChunk(file, index, chunkSize) {
    return new Promise((resolve) => {
        const start = index * chunkSize;
        const end = start + chunkSize;
        const spark = new sparkmd5.ArrayBuffer();
        const fileReader = new FileReader();
        /**
         * onload 
         * @param {*} e  -> file.slice(start, end)
         */
        fileReader.onload = (e) => {
            // MD5
            spark.append(e.target.result);
            resolve({
                start,
                end,
                index,
                hash: spark.end(),
                // 
                content: new Blob([e.target.result])
            });
        };
        fileReader.readAsArrayBuffer(file.slice(start, end));
    });
}

/**
 * MD5
 * @param {*} file 
 * @returns 
 */
export function getMD5(file) {
    return new Promise((resolve) => {
        const spark = new sparkmd5.ArrayBuffer();
        const fileReader = new FileReader();
        fileReader.onload = (e) => {
            spark.append(e.target.result);
            resolve({
                hash: spark.end()
            });
        };
        fileReader.readAsArrayBuffer(file);
    })
}

/**
 * 
 * @param {*} file 
 * @returns 
 */
export function cutFile(file) {
    return new Promise((resolve) => {
        const result = [];
        /**
         * 
         */
        const chunkCount = Math.ceil(file.size / CHUNK_SIZE);
        /**
         * 
         */
        const workerChunkCount = Math.ceil(chunkCount / THREAD_COUNT);
        let finishCount = 0;
        for (let i = 0; i < THREAD_COUNT; i++) {
            //  Worker 
            const worker = new Worker(
                new URL(
                    './worker.js',
                    import.meta.url
                )
            );
            // 
            const startIndex = i * workerChunkCount;
            let endIndex = startIndex + workerChunkCount;
            if (endIndex > chunkCount) {
                endIndex = chunkCount;
            }
            /**
             * 
             */
            worker.postMessage({
                file,
                CHUNK_SIZE,
                startIndex,
                endIndex,
            });
            /**
             * 
             * @param {*} e worker.jsonmessage
             */
            worker.onmessage = (e) => {
                for (let i = startIndex; i < endIndex; i++) {
                    result[i] = e.data[i - startIndex];
                }
                // 
                worker.terminate();
                finishCount++;
                if (finishCount === THREAD_COUNT) {
                    resolve(result);
                }
            };
        }
    });
}


